import Card from "@mui/material/Card";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";

import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";

import FavoriteIcon from "@mui/icons-material/Favorite";
import ShareIcon from "@mui/icons-material/Share";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { favActions } from "../../store/favourite-slice";

const CardComp = ({ data }) => {
  const [favColor, setFavColor] = useState("");
  const favItems = useSelector((state) => state.fav.favItems);
  const dispatch = useDispatch();

  useEffect(() => {
    // console.log("inside useEffect");
    if (favItems.includes(data.recipe.label)) {
      setFavColor("red");
    }
  }, [favItems, data.recipe.label]);

  const favouriteClickHandler = (data) => {
    if (favColor === "red") {
      setFavColor("");
      dispatch(favActions.removeFavourite(data));
    } else {
      dispatch(favActions.addFavourite(data));
      setFavColor("red");
    }
  };

  return (
    <Card sx={{ maxWidth: 345 }}>
      {/* <CardHeader
        title={data.recipe.label}
        // title={data.name}

        sx={{
          display: "block",
          // width: "30vw",
          transitionDuration: "0.3s",
          height: "8vw",
        }}
      /> */}

      <CardMedia
        component="img"
        height="194"
        image={data.recipe.image}
        // image={data.image}
        alt={data.recipe.label}
        // alt={data.name}
        sx={{ objectFit: "cover" }}
      />
      <CardContent sx={{ height: "90px" }}>
        <Typography variant="body1" component="span" color="text.secondary">
          {data.recipe.label}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton
          component="button"
          aria-label="add to favorites"
          onClick={() => favouriteClickHandler(data.recipe.label)}
        >
          <FavoriteIcon sx={{ color: favColor }} />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
      </CardActions>
    </Card>
  );
};

export default CardComp;
